  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  color: #333333;
  margin-bottom: 20px;
}
.entityExtentionheaderTitle {
  font-size: 30px;
  letter-spacing: 0;
}

.headerDescWrapper {
  font-family: "Roboto", sans-serif;
  color: #333333;
  margin-bottom: 20px;
  display: -webkit-box;
