/* Entity variable form */

.width75percent {
  width: 75%;
}
.width45percent {
  width: 45%;
}
.width15percent {
  width: 15%;
}

.entityVariablesFormContainer {
  font-family: "Roboto", sans-serif;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
}
.entityVariableFormTitle {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 22px;
  color: #333333;
}
.entityVariableFlexWrapper {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}
.entityVariableInputWrapper {
  margin: 20px 0;
  position: relative;
}

.alignCenter {
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.entityVariableFlexWrapper .ant-switch-checked {
  background-color: #55dba2;
}

.entityFieldTableHeader {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 50px;
}

.nohoverTableWrapper
  .ant-table-tbody
  > tr:hover:not(.ant-table-expanded-row)
  > td {
  background: unset !important;
}

.noHeightTable .ant-table-content {
  display: none;
}
.entityExtendcolumn {
  text-align: left;
  font: Regular 14px/17px Roboto;
  letter-spacing: 0;
  color: #333333;
  opacity: 1;
}
.entityExtendcolumnTitle {
  text-align: left;
  font: Medium 11px/39px Roboto;
  letter-spacing: 0;
  color: #999999;
  text-transform: uppercase;
  opacity: 1;
}
